body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

label {
  display: inline-block;
}

button {
  border-radius: 0;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  appearance: none;
}

[type=search]::-webkit-search-decoration {
  appearance: none;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
  unicode-range: "U+0000-00FF", "U+0131", "U+0152-0153", "U+02BB-02BC", "U+02C6", "U+02DA", "U+02DC", "U+2000-206F", "U+2074", "U+20AC", "U+2122", "U+2191", "U+2193", "U+2212", "U+2215", "U+FEFF", "U+FFFD";
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
  unicode-range: "U+0000-00FF", "U+0131", "U+0152-0153", "U+02BB-02BC", "U+02C6", "U+02DA", "U+02DC", "U+2000-206F", "U+2074", "U+20AC", "U+2122", "U+2191", "U+2193", "U+2212", "U+2215", "U+FEFF", "U+FFFD";
}
@font-face {
  font-family: "Kaushan Script";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/kaushan-script-400.woff2") format("woff2"), url("../fonts/kaushan-script-400.woff") format("woff");
  unicode-range: "U+0000-00FF", "U+0131", "U+0152-0153", "U+02BB-02BC", "U+02C6", "U+02DA", "U+02DC", "U+2000-206F", "U+2074", "U+20AC", "U+2122", "U+2191", "U+2193", "U+2212", "U+2215", "U+FEFF", "U+FFFD";
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/roboto-300ital.woff2") format("woff2"), url("../fonts/roboto-300ital.woff") format("woff");
  unicode-range: "U+0000-00FF", "U+0131", "U+0152-0153", "U+02BB-02BC", "U+02C6", "U+02DA", "U+02DC", "U+2000-206F", "U+2074", "U+20AC", "U+2122", "U+2191", "U+2193", "U+2212", "U+2215", "U+FEFF", "U+FFFD";
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-400.woff2") format("woff2"), url("../fonts/roboto-400.woff") format("woff");
  unicode-range: "U+0000-00FF", "U+0131", "U+0152-0153", "U+02BB-02BC", "U+02C6", "U+02DA", "U+02DC", "U+2000-206F", "U+2074", "U+20AC", "U+2122", "U+2191", "U+2193", "U+2212", "U+2215", "U+FEFF", "U+FFFD";
}
:root {
  --color-white: #fff;
  --color-black: #000;
  --color-grey: #999;
  --color-light-grey: #ccc;
  --color-dark-grey: #333;
  --color-simple-grey: #f8f8f8;
  --color-simple-white: #f5f5f5;
  --color-border: #e5e5e5;
  --color-yellow: #fce38a;
  --color-red: #f38181;
  --color-second-red: #eb8b8d;
  --color-green: #95e1d3;
}

html {
  color: var(--color-dark-grey);
  font-family: "Montserrat", "Arial", "Helvetica Neue", "Helvetica", sans-serif;
  font-size: 15px;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  background-color: var(--color-white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

svg {
  fill: currentColor;
}

.overflow-hidden {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.swiper {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 768px) {
  .swiper {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 768px) {
  .swiper {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.swiper-slide {
  display: grid;
  grid-template-rows: min-content;
  gap: 30px;
  height: auto;
}
@media (min-width: 768px) {
  .swiper-slide {
    grid-template-columns: 150px 1fr;
    align-items: center;
    align-self: center;
    gap: 60px;
    padding-left: 85px;
    padding-right: 85px;
    box-sizing: border-box;
  }
}

.swiper-button-prev,
.swiper-button-next {
  color: var(--color-light-grey);
  top: 75px;
}
@media (min-width: 768px) {
  .swiper-button-prev,
.swiper-button-next {
    top: 50%;
  }
}

.simplebar-track {
  background-color: var(--color-simple-white);
  border-radius: 3px;
}

.simplebar-scrollbar::before {
  background-color: var(--color-green);
  border-radius: 3px;
  opacity: 1;
}

.simplebar-scrollbar.simplebar-visible::before {
  opacity: 1;
}

.simplebar-track.simplebar-vertical {
  width: 5px;
  top: 20px;
  right: 24px;
  bottom: 20px;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
  width: 5px;
  top: 0px;
  left: 0;
  right: auto;
  bottom: 0px;
}

.simplebar-track.simplebar-horizontal {
  height: 5px;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
  height: 5px;
  top: 0;
}

.header {
  color: var(--color-white);
  text-align: center;
  background-color: var(--color-red);
  background-image: linear-gradient(rgba(243, 129, 129, 0.85), rgba(252, 227, 138, 0.85)), url("../img/backgrounds/header.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.header__container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  display: grid;
  align-content: space-between;
  height: 100vh;
  min-height: 360px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .header__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 768px) {
  .header__container {
    min-height: 500px;
  }
}
@media (min-width: 992px) {
  .header__container {
    min-height: 680px;
  }
}
@media (min-width: 1260px) {
  .header__container {
    min-height: 800px;
    padding-top: 24px;
    padding-left: 0;
    padding-right: 0;
  }
}

.header__top {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 15px;
  padding-bottom: 15px;
}
@media (min-width: 992px) {
  .header__top {
    align-items: baseline;
  }
}

.header__nav {
  margin-left: auto;
}
@media (max-width: 992px) {
  .header__nav {
    order: 3;
    margin-left: 20px;
  }
}

.header__wrap {
  display: grid;
  grid-auto-flow: column;
  column-gap: 40px;
  margin-left: 50px;
}
@media (max-width: 992px) {
  .header__wrap {
    column-gap: 20px;
    margin-left: auto;
  }
}

.header__link {
  color: var(--color-white);
  padding-bottom: 8px;
  border-bottom: 3px solid transparent;
  transition: 0.2s;
}
.header__link:hover, .header__link:focus-visible {
  color: var(--color-yellow);
  border-color: var(--color-yellow);
}
@media (max-width: 992px) {
  .header__link {
    padding-bottom: 0;
    border: none;
  }
}

.logo {
  font-size: calc(1.325rem + 0.9vw);
  color: var(--color-white);
  font-weight: 700;
  line-height: 1;
  position: relative;
  z-index: 10;
}
@media (min-width: 1200px) {
  .logo {
    font-size: 2rem;
  }
}

.nav {
  display: flex;
  align-items: center;
}
.nav--opened {
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  margin-left: 0;
  padding-top: 60px;
  background-color: var(--color-second-red);
  box-sizing: border-box;
  overflow-y: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
}
.nav--opened .nav__toggle {
  position: absolute;
  top: 17px;
  right: 15px;
}
.nav--opened .nav__toggle-item {
  height: 0;
}
.nav--opened .nav__toggle-item::before {
  transform: rotate(45deg);
}
.nav--opened .nav__toggle-item::after {
  transform: rotate(-45deg);
}
.nav--opened .nav__list {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.nav--opened .nav__item {
  margin-right: 0;
}
.nav--opened .nav__link {
  text-align: left;
  padding-top: 8px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
  }
}
@media (min-width: 992px) {
  .nav .nav__toggle {
    display: none;
  }
  .nav .nav__list {
    display: flex;
  }
}

.nav__toggle {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 16px;
}

.nav__toggle-item {
  display: block;
  height: 2px;
  background-color: var(--color-white);
  position: relative;
}
.nav__toggle-item::before, .nav__toggle-item::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: inherit;
  transition: 0.2s;
  position: absolute;
  left: 0;
}
.nav__toggle-item::before {
  transform: translateY(-6px);
}
.nav__toggle-item::after {
  transform: translateY(6px);
}

.nav__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: none;
}

.nav__item {
  margin-right: 56px;
}
.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: var(--color-white);
  padding-bottom: 8px;
  border-bottom: 3px solid transparent;
  transition: 0.2s;
  display: block;
  color: var(--color-white);
  font-size: 14px;
  text-transform: uppercase;
}
.nav__link:hover, .nav__link:focus-visible {
  color: var(--color-yellow);
  border-color: var(--color-yellow);
}
.nav__link--active {
  color: var(--color-yellow);
  border-color: var(--color-yellow);
}
@media (max-width: 992px) {
  .nav__link--active {
    border-color: transparent;
  }
}

.intro {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}
@media (min-width: 768px) {
  .intro {
    width: 75%;
  }
}

.intro__wrap {
  display: flex;
  width: 100%;
}

.intro__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-shrink: 0;
  width: 100%;
}

.intro__title {
  font-size: calc(1.725rem + 5.7vw);
  margin-bottom: calc(1.458rem + 2.496vw);
  line-height: 0.9;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 1200px) {
  .intro__title {
    font-size: 6rem;
  }
}
@media (min-width: 1200px) {
  .intro__title {
    margin-bottom: 3.33rem;
  }
}
.intro__title::after {
  margin-top: calc(1.591rem + 4.092vw);
  display: block;
  content: "";
  width: 60px;
  height: 3px;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--color-white);
}
@media (min-width: 1200px) {
  .intro__title::after {
    margin-top: 4.66rem;
  }
}
@media (min-width: 1260px) {
  .intro__title {
    font-size: calc(2.125rem + 10.5vw);
  }
}
@media (min-width: 1260px) and (min-width: 1200px) {
  .intro__title {
    font-size: 10rem;
  }
}

.intro__subtitle {
  font-size: calc(1.425rem + 2.1vw);
  display: block;
  font-family: "Kaushan Script", "Candara", "Calibri", "Segoe UI", "Optima", cursive;
  margin-bottom: 15px;
}
@media (min-width: 1200px) {
  .intro__subtitle {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .intro__subtitle {
    font-size: calc(1.605rem + 4.26vw);
    margin-bottom: calc(1.325rem + 0.9vw);
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .intro__subtitle {
    font-size: 4.8rem;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .intro__subtitle {
    margin-bottom: 2rem;
  }
}

.control {
  width: 100%;
  overflow: hidden;
}

.control__wrap {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
  transform: none !important;
}

.control__button {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  color: var(--color-white);
  font-size: 0;
  text-transform: uppercase;
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 3px solid var(--color-white);
  transition: 0.2s;
  cursor: pointer;
  opacity: 0.7;
  position: relative;
}
.control__button:hover, .control__button:focus {
  opacity: 1;
}
.control__button--active {
  opacity: 1;
}
.control__button--active::before {
  content: "";
  width: 35%;
  height: 3px;
  background-color: var(--color-red);
  position: absolute;
  top: -3px;
  left: 0;
}
.control__button--active:hover, .control__button--active:focus {
  color: var(--color-white);
  border-color: var(--color-white);
}
@media (min-width: 768px) {
  .control__button {
    font-size: 18px;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .control__button--active::before {
    width: 25%;
  }
}

.control__number {
  font-size: 24px;
  font-weight: 700;
  margin-right: 5px;
}

.button {
  padding: 0.66rem calc(1.325rem + 0.9vw);
  display: inline-block;
  color: var(--color-white);
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  background: none;
  border: 3px solid var(--color-white);
  transition: 0.2s;
}
@media (min-width: 1200px) {
  .button {
    padding: 0.66rem 2rem;
  }
}
.button:hover, .button:focus {
  color: var(--color-black);
  background-color: var(--color-white);
  border-color: var(--color-red);
}
.button:active {
  opacity: 0.8;
}

.main {
  overflow: hidden;
}

.about {
  padding-top: calc(1.691rem + 5.292vw);
  padding-bottom: calc(1.858rem + 7.296vw);
}
@media (min-width: 1200px) {
  .about {
    padding-top: 5.66rem;
  }
}
@media (min-width: 1200px) {
  .about {
    padding-bottom: 7.33rem;
  }
}

.about__container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 768px) {
  .about__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.about__cards {
  margin-top: calc(1.691rem + 5.292vw);
}
@media (min-width: 1200px) {
  .about__cards {
    margin-top: 5.66rem;
  }
}

.about__cards-img {
  width: 380px;
  height: 250px;
}

.cards {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(290px, max-content));
  gap: 30px;
}

.cards__item-wrap {
  background-color: var(--color-green);
  cursor: pointer;
  position: relative;
}
.cards__item-wrap:hover .cards__img-container, .cards__item-wrap:focus .cards__img-container {
  transform: translate(-10px, -10px);
  box-shadow: 10px 10px 0 var(--color-green);
}
.cards__item-wrap:hover .cards__img, .cards__item-wrap:focus .cards__img {
  opacity: 0.1;
}
.cards__item-wrap:hover .cards__social,
.cards__item-wrap:hover .cards__item-text, .cards__item-wrap:focus .cards__social,
.cards__item-wrap:focus .cards__item-text {
  opacity: 1;
}

.cards__img-container {
  background-image: linear-gradient(#f38181, #fce38a);
  transition: 0.2s linear;
}

.cards__img {
  object-fit: cover;
  transition: 0.2s;
}

.cards__item-text {
  color: var(--color-white);
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s;
}
.cards__item-text::before {
  display: block;
  content: "";
  width: 32px;
  height: 23px;
  margin: 0 auto 10px;
  background-image: url("../img/icons/user.png");
  background-repeat: no-repeat;
}

.cards__social {
  display: flex;
  opacity: 0;
  transition: 0.2s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cards__link {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-red);
  width: 55px;
  height: 55px;
  background-color: var(--color-yellow);
  transition: 0.2s;
  outline: none;
}
.cards__link + .cards__link {
  border-left: 1px solid var(--color-red);
}
.cards__link:hover, .cards__link:focus {
  color: var(--color-white);
  background-color: var(--color-red);
}

.cards__info {
  text-align: center;
  margin-top: 25px;
}

.cards__title {
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.cards__subtitle {
  color: var(--color-grey);
  font-family: "Roboto", "Tahoma", "Verdana", "Segoe", sans-serif;
  line-height: 1.6;
  font-weight: 300;
  font-style: italic;
}

.section-header {
  text-align: center;
}

.section-header__subtitle {
  font-size: calc(1.275rem + 0.3vw);
  margin-bottom: 0.625rem;
  display: block;
  font-family: "Kaushan Script", "Candara", "Calibri", "Segoe UI", "Optima", cursive;
  text-transform: none;
}
@media (min-width: 1200px) {
  .section-header__subtitle {
    font-size: 1.5rem;
  }
}

.section-header__title {
  font-size: calc(1.325rem + 0.9vw);
  margin-bottom: calc(1.375rem + 1.5vw);
  text-transform: uppercase;
}
@media (min-width: 1200px) {
  .section-header__title {
    font-size: 2rem;
  }
}
@media (min-width: 1200px) {
  .section-header__title {
    margin-bottom: 2.5rem;
  }
}
.section-header__title::after {
  display: block;
  content: "";
  width: 60px;
  height: 3px;
  margin: 0 auto;
  background-color: var(--color-red);
  margin-top: calc(1.325rem + 0.9vw);
}
@media (min-width: 1200px) {
  .section-header__title::after {
    margin-top: 2rem;
  }
}
.section-header__title:last-child {
  margin-bottom: 0;
}

.section-header__text {
