  background-image: url("../img/icons/equalizer.png");
}
.accordion__button--bullseye::before {
  background-image: url("../img/icons/bullseye.png");
}
.accordion__button--active .accordion__button-arrow {
  color: var(--color-green);
  transform: translateY(-50%) rotate(180deg);
}
.accordion__button--active + .accordion__content {
  max-height: 180px;
  padding-top: 20px;
  border-color: var(--color-border);
  overflow: visible;
}

