.modal {
  display: none;
  width: 80vw;
  height: 60vh;
  margin-top: -30vh;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--color-white);
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.15);
  position: fixed;
  top: 50%;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
}
.modal::before {
  content: "";
  width: 100%;
  height: 7px;
  background-color: #ff5357;
  position: absolute;
  top: -7px;
  left: 0;
}
.modal::after {
  content: "";
  width: 100vw;
  height: 100vh;
  margin-top: -1px;
  background-color: var(--color-dark-grey);
  filter: blur(100px);
  opacity: 0.4;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.modal--active {
  display: block;
  animation: bounce 0.6s;
}

.modal__map {
  width: 100%;
  height: 100%;
}

.modal__close {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  color: #ff5357;
  position: absolute;
  top: 15px;
  right: 15px;
  transition: 0.2s;
}
.modal__close:hover, .modal__close:focus-visible {
  transform: rotate(180deg);
}

.modal__close-icon {
  fill: transparent;
  stroke: currentColor;
  transform: rotate(45deg);
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
